	module.exports = {"data":{"site":{"siteMetadata":{"rootURL":"https://naoty.github.io"}},"markdownRemark":{"frontmatter":{"title":"よく使うLaTeXコマンド","time":"2018-01-08T08:57:00+0900"},"html":"<p>最近、Jupyter Notebookを使って機械学習を学び始めた。数式が頻繁に出てくるため、LaTeXを初めて使うようになった。</p>\n<p>自分のためのメモとして、よく使う数式とその記法をまとめた。</p>\n<table>\n  <thead>\n    <tr>\n      <th>記法</th>\n      <th>数式</th>\n    </tr>\n  </thead>\n  <tbody>\n    <tr>\n      <td>`$x^{-2}$`</td>\n      <td>$x^{-2}$</td>\n    </tr>\n    <tr>\n      <td>`$x_{12}$`</td>\n      <td>$x\\_{12}$</td>\n    </tr>\n    <tr>\n      <td>`$\\frac{1}{2}$`</td>\n      <td>$\\frac{1}{2}$</td>\n    </tr>\n    <tr>\n      <td>`$y = 2x + 1 \\tag{1}$`</td>\n      <td>$y = 2x + 1 \\tag{1}$</td>\n    </tr>\n    <tr>\n      <td>`$\\frac{\\partial L}{\\partial W}$`</td>\n      <td>$\\frac{\\partial L}{\\partial W}$</td>\n    </tr>\n    <tr>\n      <td><pre><code>$$\n\\begin{align}\nx + y = 3 \\tag{1} \\\\\\\n2x - y = 0 \\tag{2}\n\\end{align}\n$$</code></pre></td>\n      <td>$$\n\\begin{align}\nx + y = 3 \\tag{1} \\\\\\\n2x - y = 0 \\tag{2}\n\\end{align}\n$$</td>\n    </tr>\n    <tr>\n      <td><pre><code>$$\ny = \\begin{cases}\nx & (x > 0) \\\\\\\n0 & (x \\leqq 0)\n\\end{cases}\n$$</code></pre>\n      </td>\n      <td>$$\ny = \\begin{cases}\nx & (x > 0) \\\\\\\n0 & (x \\leqq 0)\n\\end{cases}\n$$</td>\n    </tr>\n    <tr>\n      <td><pre><code>$$\n\\sum\\_{i=0}^{k} x\\_{k}\n$$</code></pre></td>\n      <td>$$\n\\sum\\_{i=0}^{k} x\\_k\n$$</td>\n    </tr>\n    <tr>\n      <td><pre><code>$$\n\\begin{pmatrix}\n1 & 2 \\\\\\\n3 & 4\n\\end{pmatrix}\n$$</code></pre></td>\n      <td>$$\n\\begin{pmatrix}\n1 & 2 \\\\\\\n3 & 4\n\\end{pmatrix}\n$$</td>\n    </tr>\n  </tbody>\n</table>","fileAbsolutePath":"contents/posts/12.md"}},"pathContext":{"id":"contents/posts/12.md absPath of file >>> MarkdownRemark"}}

